import ImReady from '@egjs/imready';
import { onBeforeUnmount, onMounted, shallowRef, Ref, ref } from 'vue'
import { ImReadyProps } from './types'

export function useImReady(props: Partial<ImReadyProps> = {}) {
  const {
    usePreReady = true,
    usePreReadyElement = true,
    useReady = true,
    useReadyElement = true,
    useError = true,
    selector = "",
    ...options
  } = props;

  const children: HTMLElement[] = [];
  const preReadyCount = ref(0);
  const readyCount = ref(0);
  const errorCount = ref(0);
  const totalErrorCount = ref(0);
  const totalCount = ref(0);
  const isPreReady = ref(false);
  const isReady = ref(false);
  const isPreReadyOver = ref(false);
  const hasError = ref(false);
  const imRef = shallowRef<ImReady>();

  function register<T extends HTMLElement>(ref?: Ref<T> | ((el: T) => any)) {
    return (instance: T) => {
      if (instance) {
        children.push(instance);
      }
      if (!ref) {
        return;
      }
      if (typeof ref === "function") {
        ref(instance);
      } else {
        ref.value = instance;
      }
    };
  }

  onMounted(() => {
    const im = new ImReady(options);

    imRef.value = im;

    let checkedElements = children;

    if (selector) {
      checkedElements = checkedElements.reduce((prev, cur) => {
        return [...prev, ...cur.querySelectorAll<HTMLElement>(selector)];
      }, [] as HTMLElement[]);
    }
    console.log(children, checkedElements);
    im.check(checkedElements).on("error", e => {
      if (useError) {
        hasError.value = true;
        errorCount.value = e.errorCount;
        totalErrorCount.value = e.totalErrorCount;
      }
    }).on("preReadyElement", e => {
      if (usePreReadyElement) {
        preReadyCount.value = e.preReadyCount;
      }
    }).on("readyElement", e => {
      if (useReadyElement) {
        readyCount.value = e.readyCount;
        isPreReadyOver.value = e.isPreReadyOver;
      }
    }).on("preReady", () => {
      if (usePreReady) {
        isPreReady.value = true;
      }
    }).on("ready", () => {
      if (useReady) {
        isReady.value = true;
      }
    });
    preReadyCount.value = 0;
    readyCount.value = 0;
    errorCount.value = 0;
    totalErrorCount.value = 0;
    totalCount.value = im.getTotalCount();
    isReady.value = false;
    isPreReady.value = false;
    hasError.value = false;
    isPreReadyOver.value = false;
  });
  onBeforeUnmount(() => {
    imRef.value?.destroy();
  });

  return {
    preReadyCount,
    readyCount,
    errorCount,
    totalErrorCount,
    totalCount,
    isReady,
    isPreReady,
    hasError,
    isPreReadyOver,
    register,
  };
}
